@import "../scss/variables";


div#frontis-blocks-sidebar\:frontis-blocks-sidebar {
	.components-panel__header {
		&.interface-complementary-area-header {
			min-height: 58px;
			background: $white-color;
			padding: 14px 16px;

			.interface-complementary-area-header__title {
				color: $neutral-color-950;
				font-family: $heading-font-family;
				font-size: 14px;
				font-weight: 600;
				letter-spacing: 0.00px;
				line-height: 19px;
			}
		}
	}

	.fb-global-back {
		padding-left: 0;
		gap: 10px;
		display: flex;
		align-items: center;

		span {
			svg {
				fill: none !important;
			}
		}
	}

	.fb-setting-spacing-inner-container {
		padding: 0 10px;
	}

	.fb-setting-spacing-label-container {
		margin: 14px -7px !important;
	}

	.fb-global-color-panel .components-tools-panel-header {
		padding-bottom: 16px;
	}

	.fb-custom-color-panel {
		border-right: 1px solid rgb(221, 221, 221);
		border-left: 1px solid rgb(221, 221, 221);

		.components-tools-panel-header {
			padding-bottom: 16px;
		}
	}

	.fb-gradient-color-panel {
		border-top: 1px solid rgb(221, 221, 221);
		border-right: 1px solid rgb(221, 221, 221);
		border-left: 1px solid rgb(221, 221, 221);
	}

	.fb-blocks-color-tabs {
		padding: 0 16px;
		.components-tab-panel__tabs {
			padding-left: 16px;
			border-top: 1px solid rgb(221, 221, 221);
			border-left: 1px solid rgb(221, 221, 221);
			border-right: 1px solid rgb(221, 221, 221);

			button {
				font-size: $medium-font-size-global;
				font-weight: 500;
				line-height: 16px;
				border-bottom: 2px solid transparent;

				&.active-tab {
					// border-bottom: 2px solid $neutral-color-200;
				}

				&:first-child {
					padding-left: 0;
				}
			}
		}

		.components-tab-panel__tab-content {
			.components-tools-panel {
				.components-flex {
					&.components-h-stack {
						padding-top: 16px;
						padding-left: 12px;
						padding-right: 3px;

						button {
							svg {
								fill: $neutral-color-600;
							}
						}
					}
				}

				.components-tools-panel-item {
					border-left: 0;
					border-right: 0;
					margin-top: 0;

					.components-button {
						border-right: 0;
						border-left: 0;
						//padding: 14px 12px;
						padding: 0 12px;
						height: 48px;

						.components-flex-item {
							color: $neutral-color-700;
							font-family: $body-font-family;
							font-weight: 500;
							font-size: 14px;
							line-height: 16px;
						}
					}

					.components-flex {
						&.components-h-stack {
							padding-top: 0;
							padding-left: 0;
						}
					}
				}
			}
		}

		.add-custom-color {
			text-align: center;
			padding: 14px 0;

			.add-custom-color-btn {
				padding: 12px 14px;
				background: $primary-color;
				height: 44px;
				border-radius: 6px;
				font-family: $body-font-family;
				font-weight: 500;
				color: $white-color;
				font-size: 14px;
				line-height: 16px;
				align-items: center;
				gap: 10px;

				span {
					height: 100%;
					display: flex;
					align-items: center;
				}
			}
		}
	}
	.fb-global-controls {
		&.components-panel {
			border-top: 0;
			margin-top: 12px;
			padding: 16px 0;

			.fb-panel-control {
				padding: 0px;

				.fb-tab-element {
					margin-bottom: 10px;
					padding: 0 16px;
				}

				.components-navigator-button {

					font-size: $medium-font-size-global;
					font-weight: 400;
					line-height: 16px;
					padding: 0;
				}

				.fb-tab-wrapper {
					display: flex;
					align-items: center;
					flex-direction: row;
					gap: calc(8px);
					-webkit-box-pack: start;
					justify-content: flex-start;
					width: 100%;

					.fb-tab-icon {
						svg {
							fill: none !important;
						}
					}

					.fb-tab-title {

						font-size: $medium-font-size-global;
						font-weight: 400;
						line-height: 16px;
						color: $neutral-color-700-global;
					}

					&:hover {

						.fb-tab-title,
						.fb-tab-icon path {
							color: $primary-color-base;
						}
					}
				}

				.fb-global-header-inner-wrapper {
					display: flex;
					gap: 8px;
					padding: 0 16px;
					.fb-global-header-inner {
						h3 {
							font-size: $medium-font-size-global;
							font-weight: 500;
							line-height: 16px;
							color: $neutral-color-950;
							text-transform: capitalize;
							margin-top: 9px;
						}

						p {

							font-size: $medium-font-size-global;
							font-weight: 400;
							line-height: 20px;
							color: $neutral-color-950;
						}
					}

					svg {
						fill: none !important;
						&:hover {
							path {
								stroke: $primary-color-base;
							}
						}
					}

				}

				.fb-global-font-sizes-presets {
					.font-size-presettes-wrapper {
						display: flex;
						align-items: center;
						justify-content: space-between;
					}
				}
			}

			.components-panel__body {
				border: none;
				margin-bottom: 10px;
				padding: 0 16px;

				.components-panel__body-title {
					margin-bottom: 0;

					.components-panel__body-toggle {
						&.components-button {
							padding: 16px 12px;
							background: $neutral-color-100;
							border-radius: 6px;

							.components-panel__arrow {
								right: 5px;
								color: $neutral-color-600;
							}
						}
					}
				}

				&.is-opened {
					padding: 0 32px;
					padding-top: 16px;

					.components-panel__body-toggle {
						&.components-button {
							border-bottom-right-radius: 0;
							border-bottom-left-radius: 0;
						}
					}

					.fb-blocks-color-tabs {
						border: 1px solid $neutral-color-100;
						border-top: 0;
					}
				}

				&:first-child {
					padding-top: 16px;

					&.is-opened {
						padding-top: 32px;
					}
				}

				&:last-child {
					margin-bottom: 0;
				}

				.fb-blocks-color-tabs {
					border-top: 0;
					width: calc(100% + 32px);
					margin-left: calc(0% - 16px);

					.fb-global-color-panel {
						padding: 0;

					}
				}

				.components-base-control {
					width: calc(100% + 32px);
					margin-left: calc(0% - 16px);

					.components-textarea-control__input {
						margin-top: 0;
						//width: calc(100% + 32px);
						//margin-left: calc(0% - 16px);
						border: 1px solid $neutral-color-100;
					}
				}
			}


			.fb-global-typography-element-content {
				padding-top: 10px;

				.fb-setting-label-text{
					font-weight: 500;
					font-size: 11px;
					text-transform: uppercase !important;
					font-family: $body-font-family;
				}

				.fb-typography-settings {
					padding: 0;
					margin-top: 10px;

					.components-input-control__container {
						.components-input-control__input {
							&:focus {
								outline: none;
							}
						}

						.components-select-control__input {
							background: $neutral-color-50;
							height: unset;
							padding: 6px 12px;

							&:focus {
								outline: none;
							}
						}

						.components-input-control__backdrop {
							border: none;
						}
					}

					.components-range-control__wrapper {
						span {
							&:nth-child(2) {
								height: 2px;
								background: $neutral-color-200;
							}

							&:nth-child(3) {
								height: 2px;
								background: $primary-color-base;
							}
						}

						.components-range-control__thumb-wrapper {
							span {
								background-color: $primary-color-base;
							}
						}

					}

					.components-range-control__number {
						.components-input-control__container {
							border: 1px solid $primary-color-base;
							border-radius: 6px;
							margin-left: 10px;
						}
					}

					.fb-setting-spacing-label-container {
						margin: 14px 0;
					}

					.components-input-control__label {
						margin: 10px 0;
						font-weight: 500;
						font-family: $body-font-family;
					}
				}

				.components-base-control {
					width: auto;
					margin-left: 0;

					.fb-global-typography-headings-wrap {
						margin: 0 auto;
						margin-top: 12px;
					}
				}

				&:empty {
					display: none;
				}
			}

			.font_preview_box {
				align-items: center;
				background: $neutral-color-100;
				border-radius: 2px;
				display: flex;
				justify-content: center;
				margin-bottom: 16px;
				min-height: 100px;
				overflow: hidden;
			}
		}
	}
}

.block-editor-tools-panel-color-gradient-settings__dropdown>button.is-open {
	background: transparent;
}

.components-grid.components-tools-panel.block-editor-panel-color-gradient-settings.fb-color-panel.fb-global-color-panel {
	padding: 0;
	border-left: 1px solid rgb(221, 221, 221);
    border-right: 1px solid rgb(221, 221, 221);
}

.components-grid.components-tools-panel.block-editor-panel-color-gradient-settings.fb-color-panel.fb-custom-color-panel {
	padding: 0 !important;

	.block-editor-tools-panel-color-gradient-settings__item {
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding-right: 12px;

		.fb-delete-custom-color {
			display: none;
			border: none;
			background: transparent;
			color: $error-color;
			cursor: pointer;

			&:first-of-type {
				display: block;
				/* Shudhu first button visible korbe */
			}
		}
	}
}

.fb-custom-color-item .actions {
	cursor: pointer;
	margin-right: 0;
	margin-left: auto;

	.dashicons-trash:before {
		color: $error-color;
	}
}

// Gradient color
.fb-color-panel.fb-gradient-color-panel {
	.components-panel__row.fb-gradient-color-label {
		color: $neutral-color-950;
		font-family: $heading-font-family;
		font-size: 14px;
		font-weight: 600;
		letter-spacing: 0.00px;
		line-height: 19.2px;
		padding: 14px 12px;
		border-bottom: 1px solid $neutral-color-100;
	}

	.fb-gradient-color-list {
		.fb-custom-color-item {
			display: flex;
			align-items: center;
			justify-content: space-between;
			height: 44px;
			padding: 0 12px;
			border-bottom: 1px solid $neutral-color-100;

			.item-content {
				display: flex;
				align-items: center;
				gap: 8px;
				color: $neutral-color-700;
				font-family: $body-font-family;
				font-weight: 500;
				font-size: 14px;
				line-height: 16px;
			}

			&:last-child {
				//border-bottom: none;
			}

			.actions {
				.fb-reset {
					.dashicons-image-rotate {
						cursor: pointer;

						&:before {
							color: $neutral-color-700;
							font-size: 17px;
						}
					}
				}
			}
		}
	}
}

#tab-panel-1-gradient-view {
	.fb-gradient-color-panel {
		.fb-gradient-color-label {}
	}
}

.fb-gradient-color-popup {
	.fb-gradient-color-popup-content {
		padding: 10px;
	}

	.components-circular-option-picker {
		.components-circular-option-picker__swatches {
			display: none !important;
		}
	}
}

.fb-global-typo-elements-wrap {
	// background: $neutral-color-50;

	.components-navigator-back-button {
		&:focus {
			outline: none !important;
			box-shadow: none !important;
		}
	}

	.fb-custom-element {
		border-bottom: 1px solid $neutral-color-100;
		padding: 12px;

		button {
			padding: 0;
			width: 100%;
			gap: 10px;
			font-size: 14px;
			font-weight: 400;
			color: $neutral-color-700;
			height: auto;

			&:focus {
				outline: none !important;
				box-shadow: none !important;
			}

			.font_preview {
				// background: $neutral-color-100;
				// padding: 6px;
				border-radius: 2px;
				font-size: 12px;
				// min-height: 26px;
				display: flex;
				align-items: center;
				justify-content: center;
			}
		}

		&:last-child {
			.font_preview {
				background: $primary-color-base;

				svg {
					path {
						fill: $white-color;
					}
				}
			}
		}

		&.fb-global-typography-element {
			display: flex;
			align-items: center;
			justify-content: space-between;
			border-left: 1px solid $neutral-color-100;
			border-right: 1px solid $neutral-color-100;
		}
	}

	.components-navigator-screen {
		//padding-top: 15px;
	}

	.fb-typography-settings {
		.components-base-control {
			width: auto !important;
			margin-left: 0 !important;
		}
	}

	.fb-delete-custom-typo {
		margin-left: auto;
		margin-right: 0;
		width: 20px !important;

		.dashicons-trash {
			&:before {
				color: $error-color;
			}
		}
	}
}

svg#fb-global-panel-icon {
	height: 25px;
}

.block-editor-panel-color-gradient-settings__color-name {
	font-family: $heading-font-family;
	font-size: $base-font-size;
	font-weight: 500;
	line-height: 16px;
}